import { fetchEndpointMerchant } from '../server/network'

// 模板列表
export const getShippingTplList = () => fetchEndpointMerchant('/admin/api.shippingtpl.list', 'POST', {})

// 模板删除
export const delShippingTpl = tplID =>
  fetchEndpointMerchant('/admin/api.shippingtpl.delete', 'POST', {
    id: tplID
  })

// 添加 & 编辑
export const saveShippingTpl = params =>
  fetchEndpointMerchant('/admin/api.shippingtpl.save', 'POST', {
    name: params.name,
    id: params.tplID,
    tpl_type: params.tpl_type
  })

// 配送区域列表
export const deliveryAreaList = tplID =>
  fetchEndpointMerchant('/admin/api.shippingtplarea.list', 'POST', {
    tpl_id: tplID
  })

// 配送区域删除
export const delDeliveryArea = areaID =>
  fetchEndpointMerchant('/admin/api.shippingtplarea.delete', 'POST', {
    id: areaID
  })

// 配送区域添加 & 编辑
export const saveDeliveryArea = params =>
  fetchEndpointMerchant('/admin/api.shippingtplarea.save', 'POST', {
    item: params
  })

// 获取省市
export const totalAddress = (areaID, tplID) =>
  fetchEndpointMerchant('/admin/api.shippingtplarea.get', 'POST', {
    id: areaID || '0',
    tpl_id: tplID
  })

// 获取县
export const townList = (tplID, parentID, areaID, region_type = 3) =>
  fetchEndpointMerchant('/admin/api.shippingtplarea.get.town', 'POST', {
    parent_id: parentID,
    id: areaID,
    tpl_id: tplID,
    region_type: region_type
  })
