<template>
  <div class="container">
    <!-- 标题 -->
    <page-title first-title="添加运费模板"></page-title>
    <!-- 自定义模板名称 -->
    <div class="shipping-name-wrapper">
      <span>*</span>
      <label>模板名称：</label>
      <el-input v-model="shippingName" placeholder="请自定义模板名称"></el-input>
    </div>
    <!-- 类型 -->
    <div class="shipping-name-wrapper">
      <span>*</span>
      <label>模板类型：</label>
      <el-select v-model="shippingType" :disabled="!!tplID">
        <el-option label="常规模板" :value="1"></el-option>
        <el-option label="指定条件包邮" :value="2"></el-option>
      </el-select>
    </div>
    <!-- 配送区域列表 -->
    <div class="delivery-area-wrapper" v-if="tplID">
      <p>配送区域</p>
      <el-table :data="deliveryAreaList" border stripe style="width: 100%" v-loading="loading">
        <el-table-column prop="area_list_name" label="地区"></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editArea(scope.row.id)">编辑</el-button>
            <el-button type="text" size="small" @click="deleteArea(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
        <el-table-column label="首件数" width="100">
          <template slot-scope="scope">
            <input type="text" v-model="scope.row.head" />
          </template>
        </el-table-column>
        <el-table-column label="运费" width="100">
          <template slot-scope="scope">
            <input type="text" v-model="scope.row.head_fee" />
          </template>
        </el-table-column>
        <template v-if="shippingType == 1">
          <el-table-column label="续件" width="100">
            <template slot-scope="scope">
              <input type="text" v-model="scope.row.step" />
            </template>
          </el-table-column>
          <el-table-column label="续费" width="100">
            <template slot-scope="scope">
              <input type="text" v-model="scope.row.step_fee" />
            </template>
          </el-table-column>
        </template>
        <template v-if="shippingType == 2">
          <el-table-column label="包邮条件" width="600">
            <template slot-scope="scope">
              <div class="free-wrapper">
                <el-select v-model="scope.row.free_type" placeholder="请选择包邮条件类型" size="small">
                  <el-option label="请选择包邮条件类型" :value="0" disabled></el-option>
                  <el-option label="满件" :value="1"></el-option>
                  <el-option label="满元" :value="2"></el-option>
                  <el-option label="满件或满元" :value="3"></el-option>
                </el-select>
                <div class="free-rule">
                  满
                  <template v-if="scope.row.free_type == 1 || scope.row.free_type == 3">
                    <input class="text" type="text" v-model="scope.row.free_sum" />
                    件
                  </template>
                  {{ scope.row.free_type == 3 ? '，' : '' }}
                  <template v-if="scope.row.free_type == 2 || scope.row.free_type == 3">
                    <input type="text" v-model="scope.row.free_fee" />
                    元
                  </template>
                  以上包邮
                </div>
              </div>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </div>
    <!-- 新增地区与运费 -->
    <div class="add-area-wrapper" v-if="isSaveTpl">
      <el-button type="text" @click="addNewAddr">指定地区与运费</el-button>
    </div>
    <!-- 底部按钮组 -->
    <div class="btn-group-wrapper">
      <el-button type="primary" @click="saveArea">保存</el-button>
      <el-button class="btn-back" @click="goBack">返回</el-button>
    </div>

    <!-- 区域选择 -->
    <div class="area_dialog">
      <el-dialog
        class=""
        :visible.sync="dialogVisible"
        width="60%"
        :show-close="false"
        @open="getAddress"
        @closed="isShowRegionsList = false"
      >
        <div slot="title">自定义地区邮费</div>
        <div class="check-all">
          <el-checkbox :indeterminate="isIndeterminate" v-model="isCheckAll" @change="handleCheckAllChange"
            >全选</el-checkbox
          >
        </div>
        <div class="total-address-wrapper" v-loading="isRegionsLoading">
          <!-- <p v-for="item in provinceList" :key="item.region_id">{{ item.region_name }}</p> -->
          <el-cascader-panel
            v-if="isShowRegionsList"
            ref="cascaderPanel"
            @change="checkCities"
            v-model="checkedAddress"
            :props="props"
            :options="provinceList"
          >
          </el-cascader-panel>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirmAddress">确 定</el-button>
          <el-button @click="dialogVisible = false">取 消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  deliveryAreaList,
  saveShippingTpl,
  delDeliveryArea,
  totalAddress,
  saveDeliveryArea,
  townList
} from '../../api/shippingtpl'
let id = 0
export default {
  name: 'SuppliersShippingTemplatesAdd',
  data() {
    return {
      props: {
        multiple: true,
        value: 'region_id',
        label: 'region_name',
        children: 'list',
        emitPath: false
        // expandTrigger: 'hover',
      },
      shippingName: '',
      shippingType: undefined,
      freeType: undefined,
      deliveryAreaList: [],
      // isSaveTpl: null,  //是否新增并保存了模板名称
      dialogVisible: false, // 弹框
      areaID: '', // 配送区域id
      tplID: '', // 模板id
      provinceList: [], // 省列表
      checkedAddress: [], // 选中的省市
      loading: true,

      isRegionsLoading: false,
      isShowRegionsList: false,
      citiesCount: 0, // 待选择城市的数量
      isIndeterminate: false, // 全选按钮不确定状态
      isCheckAll: false, // 全选按钮是否选中
      selectAllProvince: [], // 全选存放省份
      selectAllArea: []
    }
  },
  created() {
    // 如果是编辑，获取地区列表
    this.tplID = this.$route.query.tplID
    let obj = this.$route.query
    if (obj.tplID) {
      this.shippingName = obj.tplName
      this.shippingType = Number(obj.type)
      this.getAreaList(obj.tplID)
    }
    // this.getTown()
  },
  computed: {
    isSaveTpl() {
      return !!this.tplID
    }
  },
  methods: {
    // 获取省市
    getAddress() {
      this.checkedAddress = [] // 每次打开地区列表时，置空选中状态
      this.provinceList = [] // 每次打开地区列表时，列表先置空（要不会显示一下上次数据）
      this.selectAllArea = []
      this.citiesCount = 0
      this.isIndeterminate = false
      this.isCheckAll = false
      this.isRegionsLoading = true
      this.isShowRegionsList = true
      totalAddress(this.areaID, this.tplID)
        .then(res => {
          this.provinceList = res.area_list_msg
          this.provinceList.map(proItem => {
            // 没有市级级的，添加省级到市级
            if (!proItem.list || proItem.list.length == 0) {
              proItem.list = [{ region_id: proItem.region_id, region_name: proItem.region_name }]
              if (proItem.checked) {
                proItem.list = [
                  { region_id: proItem.region_id, region_name: proItem.region_name, checked: proItem.checked }
                ]
              }
            }
            proItem.list.map(cityItem => {
              // 没有县级的，添加市级到县级
              if (!cityItem.list || cityItem.list.length == 0) {
                cityItem.list = [{ region_id: cityItem.region_id, region_name: cityItem.region_name }]
                if (cityItem.checked) {
                  cityItem.list = [
                    { region_id: cityItem.region_id, region_name: cityItem.region_name, checked: cityItem.checked }
                  ]
                }
              }
              this.citiesCount += cityItem.list.length
              // 为全选拼接数组
              cityItem.list.forEach(townItem => {
                this.selectAllArea.push(townItem.region_id)
              })
              //（只在编辑状态下，即this.areaID不为空的情况）设置选择状态
              if (this.areaID) {
                cityItem.list.map(t => {
                  // 返回全国，设置全选状态
                  if (res.area_list && res.area_list.length === 1 && res.area_list[0] == '1') {
                    t.checked = 1
                  }
                  if (t.checked) {
                    this.checkedAddress.push(t.region_id)
                  }
                })
              }
            })
          })
          this.checkCities()
        })
        .finally(() => {
          this.isRegionsLoading = false
        })
    },

    // 获取地区列表
    getAreaList(tplID) {
      deliveryAreaList(tplID).then(res => {
        this.deliveryAreaList = res
        this.loading = false
      })
    },
    // 编辑地区
    editArea(id) {
      this.areaID = id
      this.dialogVisible = true
    },
    // 删除地区
    deleteArea(id) {
      delDeliveryArea(id).then(res => {
        let _list = this.deliveryAreaList,
          len = this.deliveryAreaList.length
        for (let i = 0; i < len; i++) {
          if (_list[i].id === id) {
            _list.splice(i, 1)
            this.$notify.success('删除成功')
            break // 删除后跳出循环，不再执行，否则报错
          }
        }
      })
    },
    // 返回
    goBack() {
      this.$router.go(-1)
    },
    // 保存
    saveArea() {
      let _n = this.shippingName
      if (!_n) {
        this.$notify.error('模板名称必填')
        return
      }
      if (!this.shippingType || this.shippingType == 0) {
        this.$notify.error('模板类型必选')
        return
      }
      let _this = this
      for (let i = 0; i < this.deliveryAreaList.length; i++) {
        let item = this.deliveryAreaList[i]
        // let reg = /^[1-9]\d*$/
        if (item.head % 1 !== 0 || item.head <= 0) {
          _this.$notify.error(`第${i + 1}条数据中，首件必须为大于0的整数`)
          return
        }
        if (item.step % 1 !== 0) {
          _this.$notify.error(`第${i + 1}条数据中，续件必须为整数`)
          return
        }
        if (isNaN(item.head_fee) || isNaN(item.step_fee) || item.head_fee < 0 || item.step_fee < 0) {
          _this.$notify.error(`第${i + 1}条数据中，费用必须为数字且大于等于0`)
          return
        }
        if (this.shippingType == 2 && item.free_type == 0) {
          _this.$notify.error(`第${i + 1}条数据中，必须选择包邮条件类型`)
          return
        }
        if (item.free_type > 0) {
          if ([1, 3].indexOf(item.free_type) > -1 && (item.free_sum % 1 !== 0 || item.free_sum <= 0)) {
            _this.$notify.error(`第${i + 1}条数据中，满件必须为大于0的整数`)
            return
          }
          if ([2, 3].indexOf(item.free_type) > -1 && (isNaN(item.free_fee) || item.free_fee <= 0)) {
            _this.$notify.error(`第${i + 1}条数据中，满元必须为大于0的数字`)
            return
          }
        }
      }
      const p1 = function() {
        return new Promise((resolve, reject) => {
          saveShippingTpl({ name: _n, tplID: _this.tplID, tpl_type: _this.shippingType }).then(
            res => {
              resolve(res)
            },
            err => {
              reject(err)
            }
          )
        })
      }
      const p2 = function() {
        return new Promise((resolve, reject) => {
          _this.deliveryAreaList.forEach(item => {
            item.area_list = JSON.stringify(item.area_list)
          })
          saveDeliveryArea(_this.deliveryAreaList).then(
            res => {
              resolve(res)
            },
            err => {
              reject(err)
            }
          )
        })
      }
      const promiseArr = []
      promiseArr.push(p1())
      if (this.deliveryAreaList.length > 0) {
        promiseArr.push(p2())
      }
      Promise.all(promiseArr).then(res => {
        this.loading = false
        this.$notify.success('保存成功')
        if (this.tplID) {
          this.goBack()
        } else if (res[0].tpl_id) {
          this.tplID = res[0].tpl_id
        }
      })
      // // 保存模板名称
      // saveShippingTpl({ name: _n, tplID: this.tplID, tpl_type: this.shippingType }).then(res => {
      //   this.$notify.success('保存成功')
      //   this.loading = false
      //   if (res.tpl_id) {
      //     this.tplID = res.tpl_id
      //   }
      // })
      // if (this.deliveryAreaList.length > 0) {
      //  saveDeliveryArea(this.deliveryAreaList)
      // }
    },
    // 弹框确定按钮
    confirmAddress() {
      if (this.checkedAddress.length == 0) {
        this.$msgbox({
          message: '请选择配送地区',
          type: 'error'
        })
        return
      }
      this.dialogVisible = false
      this.loading = true
      let _this = this
      let area_list = []
      /*if (this.isCheckAll) {
        area_list = [...this.selectAllProvince]
      } else {
        area_list = [...this.checkedAddress]
      }*/
      area_list = [...this.checkedAddress]
      // debugger
      if (!this.areaID) {
        // 新增直接调起保存接口
        let params = [
          {
            area_list: JSON.stringify(area_list),
            head: 1,
            head_fee: 0,
            step: 0,
            step_fee: 0,
            tpl_id: this.tplID,
            free_type: 0,
            free_sum: 0,
            free_fee: 0
          }
        ]
        saveDeliveryArea(params).then(() => {
          this.getAreaList(this.tplID)
        })
      } else {
        this.deliveryAreaList.forEach(item => {
          // debugger
          item.area_list = JSON.stringify(item.area_list)
          if (item.id === _this.areaID) {
            // debugger
            item.area_list = JSON.stringify(area_list)
          }
        })
        // debugger
        saveDeliveryArea(this.deliveryAreaList).then(() => {
          this.getAreaList(this.tplID)
        })
      }
    },
    // 新增配送区域
    addNewAddr() {
      this.areaID = ''
      this.dialogVisible = true
    },

    checkCities() {
      const checkedCities = this.checkedAddress.length
      this.isCheckAll = this.citiesCount == checkedCities
      this.isIndeterminate = checkedCities > 0 && checkedCities < this.citiesCount
    },

    /**
     * 点击全选按钮
     *
     * @param      {Boolean}  value   全选按钮是否选中
     */
    handleCheckAllChange(value) {
      if (value) {
        // debugger

        this.checkedAddress = this.selectAllArea
        // this.$nextTick(() => {
        //   debugger
        // })
        // this.isRegionsLoading = true
        this.isIndeterminate = false
        /*this.selectAllProvince = []

        this.provinceList.forEach(proItem => {
          if (proItem.list.legnth == 0) {
            return
          }
          this.selectAllProvince.push(proItem.region_id)
        })*/
      } else {
        this.$refs.cascaderPanel.clearCheckedNodes()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.shipping-name-wrapper {
  margin: 15px 0;
  display: flex;
  align-items: center;
  span {
    color: #f00;
    margin-right: 10px;
  }
  label {
    width: 100px;
    font-size: 18px;
  }
}
.delivery-area-wrapper {
  p {
    padding: 8px;
    background-color: lightgray;
  }
  input {
    padding: 8px;
    border-radius: 4px;
    border: 1px solid gray;
    width: 80%;
  }
  .free-rule {
    display: inline-block;
    margin-left: 10px;
    input {
      width: 80px;
    }
  }
}
.add-area-wrapper {
  margin-top: 15px;
}
.btn-group-wrapper {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  .btn-back {
    border-color: #409eff;
    color: #409eff;
    margin-left: 20px;
  }
}
.check-all {
  text-align: left;
  padding-left: 20px;
  padding-bottom: 10px;
}
</style>

<style lang="scss">
.area_dialog {
  .el-dialog__header {
    background: #505763;
    text-align: left;
    padding: 5px 20px;
    color: #fff;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
  }
  .el-dialog__body {
    text-align: center;
    .fail_info {
      text-align: left;
    }
  }
  .el-dialog__footer {
    background: #e4e4e4;
    text-align: center;
    padding: 5px 20px;
    .el-button {
      padding: 5px 32px;
    }
  }
  .total-address-wrapper {
    display: flex;
    flex-wrap: wrap;
    p {
      width: 25%;
    }
    .el-cascader-menu__wrap {
      height: 500px;
    }
  }
}
</style>
